.product__container {
    justify-content: center;

    @media screen and (min-width:320px) {}

}

// --------------product scss==============

.product {
    background-color: $secondary-light-color;
    padding-bottom: 60px;
}

.product__title-wrap {
    padding-top: 60px;
    padding-bottom: 140px;
    display: flex;
    flex-direction: column;
    display: flex;
    justify-content: center;
}

.product__pre-title {
    font-family: $font-secondary;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.17;
    text-align: center;
    text-transform: uppercase;
    color: $secondary-color;
}

.product__title {
    font-family: $font-secondary;
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: $primary-accent-color;

    margin-top: 15px;
    margin-bottom: 8px;

    @media screen and (min-width:320px) {

        margin-top: 18px;
        margin-bottom: 20px;
    }
}

.card {
    width: 100%;
    height: auto;
    padding-top: 140px;
    position: relative;

    border-radius: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
    height: auto;
    padding-top: 140px;
    position: relative;


    @media screen and (min-width:768px) {
        padding-top: 118px;
        margin-bottom: 0;

    }

}


.product__list {
    display: flex;
    flex-direction: row;
    justify-content: column;
    flex-wrap: wrap;

    @media screen and (min-width: 768px) {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 16px;
        flex-wrap: nowrap;
    }

}

.product__item {
    position: relative;
    display: flex;

    &:not(:last-child) {
        padding-bottom: 140px;
    }

    @media screen and (min-width: 768px) {
        display: flex;
        padding-bottom: 0;

        &:not(:last-child) {
            padding-bottom: 0;
        }

    }
}



.card--ice-cream:before {
    content: "";
    // background-image: url("./images/products-img-1-ice-cream-mob-1x.png");

    @media screen and (min-width: 768px) {
        // background-image: url("./images/products-img-1-ice-cream-tab-1x.png");
    }

    background-repeat: no-repeat;
    position: absolute;
    top: -140px;

    width: 280px;
    height: 280px;
    z-index: 1;
    background-position-x: center;

    @media screen and (min-width: 768px) {
        width: 224px;
        height: 224px;
    }

}


.card--ice-coffee:before {
    content: "";
    // background: url('../images/products-img-2-icecoffee-mob-1x.png');

    @media screen and (min-width: 768px) {
        // background-image: url("../images/products-img-2-icecoffee-tab-1x.png");
    }

    background-repeat: no-repeat;
    position: absolute;
    top: -140px;
    width: 280px;
    height: 280px;
    z-index: 1;
    background-position-x: center;

    @media screen and (min-width: 768px) {
        width: 224px;
        height: 224px;
    }
}




.card--milkshakes:before {
    content: "";
    // background: url("../images/products-img-3-milkshakes-mob-1x.png");

    @media screen and (min-width: 768px) {
        // background-image: url("../images/products-img-3-milkshakes-tab-1x.png");
    }

    background-repeat: no-repeat;
    position: absolute;
    top: -140px;

    width: 280px;
    height: 280px;
    z-index: 1;
    background-position-x: center;

    @media screen and (min-width: 768px) {
        width: 224px;
        height: 224px;

    }
}


.card__title {
    font-family: $font-secondary;
    font-style: normal;
    font-weight: 4000;
    font-size: 20px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $secondary-light-color;
    position: relative;
    padding-top: 20px;
    margin-bottom: 70px;

    @media screen and (min-width: 768px) {
        padding-top: 0;
        line-height: 1.76;
    }

}

.card__text:before {
    content: " ";
    height: 6px;
    width: 6px;
    border-radius: 50%;
    background-color: #FFFFFF;
    background-position-x: center;

    display: inline;
    position: absolute;
    top: -32px;
    left: 50%;
    transform: X(50%);

    box-shadow:
        -10px 0 0 #FFFFFF,
        10px 0 0 #FFFFFF;
    z-index: 3;
}

.card__text {
    font-family: $font-primary;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    text-align: center;
    letter-spacing: 0.04em;
    color: $secondary-light-color;
    position: relative;
    padding-left: 40px;
    padding-right: 40px;
}

.card-btn {
    margin-top: 40px;
    margin-bottom: 40px;

    @media screen and (min-width: 768px) {
        margin-top: 32px;

    }
}

.card--ice-cream {
    background: $primary-bg-color;
}

.card--ice-coffee {
    background: $coffe-bg-color;
}

.card--milkshakes {
    background: $milkshakes-bg-color;

}


