.button {
  color: #d41443;
  background-color: #fff;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  padding: 12px 18px;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.button:hover, .button:focus {
  background-color: #ecebeb;
}

.button--accent {
  color: #fff;
  background-color: #d41342;
}

.button--accent:hover, .button--accent:focus {
  background-color: #ed2c5c;
}

.button--small {
  border-radius: 20px;
  padding: 7px 13px;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.25;
}

.button--circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding: 0;
}

.button__icon {
  stroke: currentColor;
  margin-left: 10px;
}

.button--circle .button__icon {
  fill: currentColor;
  margin-left: 0;
}

.burger-button {
  padding: 10px 0 10px 10px;
  display: block;
}

.header {
  z-index: 10;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  padding-top: 8px;
  padding-bottom: 8px;
  position: absolute;
  top: 0;
  left: 0;
}

.header.is-fixed {
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  background-color: rgba(255, 184, 202, .4);
  position: fixed;
  top: 0;
  transform: translateY(-100%);
}

.header.is-fixed.hide {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: translateY(-100%);
}

.header.is-fixed.show {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: translateY(0);
}

@media screen and (min-width: 1200px) {
  .header.is-fixed {
    background-color: rgba(255, 184, 202, .8);
  }
}

@media screen and (min-width: 768px) {
  .header {
    top: 24px;
  }
}

.header__container {
  align-items: center;
  display: flex;
}

.header__burger {
  margin-left: auto;
}

@media screen and (min-width: 1200px) {
  .header__burger {
    display: none;
  }
}

.header__button {
  min-width: 160px;
  margin-left: 32px;
  display: none;
}

@media screen and (min-width: 768px) {
  .header__button {
    display: block;
  }
}

@media screen and (min-width: 1200px) {
  .header__button {
    box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
  }
}

.header__backdrop {
  width: 100vw;
  height: 100vh;
  visibility: hidden;
  opacity: 0;
  background: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.menu.is-open + .header__backdrop {
  visibility: visible;
  opacity: 1;
}

.menu {
  width: 100vw;
  min-height: 100vh;
  z-index: 15;
  visibility: hidden;
  background-color: #e17992;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.menu.is-open {
  visibility: visible;
  transform: translateX(0);
}

@media screen and (min-width: 768px) {
  .menu {
    width: 40%;
  }
}

@media screen and (min-width: 1200px) {
  .menu {
    width: auto;
    height: auto;
    min-height: 0;
    z-index: 10;
    visibility: visible;
    background-color: rgba(0, 0, 0, 0);
    flex-direction: row-reverse;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    position: static;
    transform: none;
  }
}

.menu__buttons {
  padding: 10px 20px 20px;
}

@media screen and (min-width: 768px) {
  .menu__buttons {
    padding: 10px 34px 20px;
  }
}

@media screen and (min-width: 1200px) {
