.franchise {
  width: calc(100% - 10px);
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%) scale(1) rotate(0);
  background-color: #fff5f6;
  border-radius: 4px;
  padding: 40px;
  transition: transform .25s linear;
  position: absolute;
  top: 50%;
  left: 50%;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 320px) {
  .franchise {
    max-width: 480px;
  }
}

@media screen and (max-width: 320px) {
  .franchise {
    max-width: 480px;
  }
}

.franchise {
  background-image: url("slider-modal.2b6a1309.png");
  background-position: 100% 100%;
  background-repeat: no-repeat;
  background-size: 60%;
}

.franchise__field {
  margin-bottom: 10px;
}

.franchise__field:nth-last-child(-2n+3) {
  margin-bottom: 20px;
}

.franchise__field:nth-last-child(-2n+2) {
  margin-bottom: 0;
}

.franchise__label {
  letter-spacing: .01em;
  color: #757575;
  margin-bottom: 4px;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.17;
  display: block;
}

.franchise__p {
  text-align: center;
  letter-spacing: .03em;
  color: #212121;
  margin-bottom: 12px;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.15;
}

.franchise__text__area {
  width: 100%;
  height: 120px;
  resize: none;
  letter-spacing: .01em;
  color: #212121;
  border: 1px solid rgba(34, 34, 34, .2);
  border-radius: 4px;
  padding: 10px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.17;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.franchise__text__area:hover, .franchise__text__area:focus {
  border: 1px solid #d41443;
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
}

.check-text {
  letter-spacing: .03em;
  color: #757575;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  line-height: 1.71;
  display: flex;
  position: relative;
}

@media screen and (max-width: 1200px) {
  .check-text {
    font-size: 12px;
    line-height: 1.17px;
  }
}

.check-text span {
  width: 16px;
  height: 15px;
  border: solid;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-right: 5px;
  display: flex;
}

.check-icon {
  fill: rgba(0, 0, 0, 0);
}

.input-check:checked + .check-text span {
  background-color: #d41443;
  border: none;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.input-check:checked + .check-text .check-icon {
  fill: #fff;
}

.input-wrap {
  position: relative;
}

.input-icon {
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
}

.franchise__input {
  width: 100%;
  height: 40px;
  resize: none;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(34, 34, 34, .2);
  border-radius: 4px;
  padding-left: 30px;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
}

.franchise__input:focus, .franchise__input:hover {
  border: 1px solid #d41443;
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
}

.franchise__input:hover + .input-icon, .franchise__input:focus + .input-icon {
